import React from "react";
import { useForm, ValidationError } from "@formspree/react";
import { useState } from "react";
import light from "../assets/light.svg";
import dark from "../assets/dark.svg";
import { NavLink } from "react-router-dom";

function ContactForm() {
  const [state, handleSubmit] = useForm("xaygjzoa");

  const [isDarkMode, setIsDarkMode] = useState(false);
  
  const variable = {
    // fontFamily: 'Artifika, serif',
    // fontFamily: 'Dancing Script, cursive',
    fontFamily: "Kaushan Script, cursive",
    fontFamily: "Lobster, cursive",
    // fontFamily: 'Montserrat, sans-serif',
    // fontFamily: 'PT Sans Narrow, sans-serif',
    // fontFamily: 'Rubik Iso, cursive',
  };

  function toggleDarkMode() {
    setIsDarkMode(!isDarkMode);
    const body = document.body;
    body.classList.toggle("black-screen");
  }

  if (state.succeeded) {
    return <div> <p className="text-center mt-28 font-extrabold text-2xl text-violet-400">Thanks for joining! </p>
    
    <NavLink to="/" className="flex justify-center items-center mt-10 font-bold text-xl text-violet-400">HOME</NavLink>
    
    </div>
    
  }
  

  return (
    <>
      <div className="m-4 flex justify-end ">
        <button
          className="bg-green-500 text-white flex  font-bold p-2 rounded-md "
          onClick={() => toggleDarkMode()}
        >
          {isDarkMode ? (
            <>
              <img className="h-6 w-6 " src={light} />
            </>
          ) : (
            <>
              <img className="h-6 w-6" src={dark} />
            </>
          )}
        </button>
        <span className={`bg-green-500 p-2 select-none rounded-md ml-5 font-bold ${isDarkMode ? "dark-mode" : "light-mode"}`}><NavLink to="/">HOME</NavLink></span>
      </div>
      <div className="text-center font-bold text-violet-500 text-2xl" >
      <h1 style={variable}>Email Chat</h1>
      </div>
      <form className="mt-14" onSubmit={handleSubmit}>
        <div className="text-center"></div>

        <div className="flex justify-center items-center mt-10">
          <input
            className="bg-slate-300 w-96 h-10 rounded-lg"
            id="email"
            type="email"
            name="email"
          />
          <ValidationError prefix="Email" field="email" errors={state.errors} />
        </div>

        <div className="flex justify-center items-center mt-10">
          <textarea
            className="bg-slate-300 w-96 h-40 rounded-lg"
            id="message"
            name="message"
          />
          <ValidationError
            prefix="Message"
            field="message"
            errors={state.errors}
          />
        </div>

        <div className="flex justify-center items-center mt-10 ">
          <button
            className="bg-green-500 text-center font-bold text-white p-3 rounded-2xl"
            type="submit"
            disabled={state.submitting}
          >
            Submit
          </button>
        </div>
      </form>
    </>
  );
}
function Formlog() {
  return <ContactForm />;
}
export default Formlog;





